import { mockItInterface } from "..";
import { loginProp } from '../../Api/modules/user'

const users = [
    {
        userName: 'joe',
        password: 'imjoe',
        userId: 0
    },
    {
        userName: 'ann',
        password: 'imAnn',
        userId: 1
    },
]

export const userMock: mockItInterface[] = [
    {
        url: '/login',
        rtype: 'post',
        func: function(prop: {body: string}) {
            const realBody = JSON.parse(prop.body) as loginProp;
            const target = users.find(user => user.userName === realBody.accountNumber);
            if (target && target.password === realBody.pwd) {
                return {
                    success: true,
                    user: target
                }
            } else {
                return {
                    success: false,
                    user: null
                }
            }
        }
    }
]