import { instance } from "..";
import QueryString from 'qs'

interface getList {
    type: boolean; // false => people | true => money
    index: number;
    tag1: string;
    tag2: string;
    findValue: string;
}
export function getList(prop: getList) {
    return instance.post('/idle/find', QueryString.stringify(prop));
}


interface getUserList {
    userId: number;
}
export function getUserList(accountnumber: string) {
    return instance.get('/idle/all', {
        params: {
            accountnumber
        }
    });
}