import { Dispatch, SetStateAction } from 'react';
import { PeopleItemProp } from '../../People'
import PeopleItem from './PeopleItem';

import FilterRender from '../../../../Component/FilterRender';

export interface ModalObj {
    setModalVisible: Dispatch<SetStateAction<boolean>>
    setCurrentId: Dispatch<SetStateAction<number>>
}

interface PeopleListProp {
    list: PeopleItemProp[];
    modal: ModalObj
}

export default function PeopleList({list, modal}: PeopleListProp) {
    return FilterRender(list,
        (item => (<PeopleItem people={item} key={item.id} modal={modal}/>)));
}
