export function getUrlParam(url: string) {
    let tmpUrl = url.split('?');
    tmpUrl = tmpUrl.slice(1, tmpUrl.length);
    let ret = {} as {[p: string]: string};
    tmpUrl.forEach(subString => {
        let tmpKeyVal = subString.split('=');
        ret[tmpKeyVal[0]] = tmpKeyVal[1];
    })
    return ret;
}