import { Card, Button, Tag } from "antd"
import { publishItem } from "../Publish"

interface PublishItemProp {
    item: publishItem,
    openModal: VoidFunction,
    deleteItem: VoidFunction
}

export default function PublishItem({item, openModal, deleteItem}: PublishItemProp) {
    return (
        <>
            <div className='m-4 p-[2px] text-left people-item-div'>
                <Card title={
                    <>
                        <p className="mr-2">{item.title}</p>
                        {item.type === 'money' ? 
                            <Tag color="gold"></Tag> :
                            <Tag color="green"></Tag>
                        }
                        {item.tags.map(tag => <Tag key={tag} color="blue">{tag}</Tag>)}
                        <Button danger type="primary" className="float-right ml-2" onClick={deleteItem}></Button>
                        <Button type="primary" className="float-right" onClick={() => {openModal()}}></Button>
                    </>
                } className="w-full" hoverable={true}>
                    <p>{item.detail}</p>
                    <p>{item.publishTime}</p>
                </Card>
            </div>
        </>
    )
}