import { instance } from '../index'
import QueryString from 'qs'

export interface registerProp {
    accountNumber: string,
    username: string,
    pwd: string
}
export function register({username, pwd, accountNumber}: registerProp): Promise<any> {
    return instance.post('/user/sign-in/', QueryString.stringify({
        accountNumber: accountNumber,
        userPassword: pwd,
        nickname: username
    }))
}

export interface loginProp {
    accountNumber: string,
    pwd: string
}

export function login({accountNumber, pwd}: loginProp): Promise<any> {
    return instance.post('/login', QueryString.stringify({
        accountnumber: accountNumber,
        password: pwd
    }, {arrayFormat: 'brackets'}))
}

interface logoutProp {
    accountNumber: string
}
export function logout({accountNumber}: logoutProp): Promise<any> {
    return instance.post('/logout', QueryString.stringify({
        accountnumber: accountNumber,
    }, {arrayFormat: 'brackets'}))
}