import { createContext, DispatchWithoutAction, ReactNode, useContext, useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { login as loginApi, loginProp, logout as logoutApi } from "../Api/modules/user";
import { message } from 'antd'

interface AuthContext {
    currentUser: User | null,
    login: (loginProp: loginProp) => Promise<void>,
    logout: VoidFunction
}

export const AuthContext = createContext<null | AuthContext>(null);

export interface User {
    account: string,
    username: string
}

export default function AuthContextProvider({children}: {children: ReactNode}) {
    const [currentUser, setCurrentUser] = useState<User | null>(null);
    const navigate = useNavigate();
    const login = async (loginProp: loginProp) => {
        try {
            let res = await loginApi(loginProp)
            console.log(res);
            if (res.data.code === 200) {
                const tempUser = {
                    account: loginProp.accountNumber,
                    username: res.data.data.nickname
                }
                message.success('');
                setCurrentUser(tempUser);            
                window.sessionStorage.setItem('currentUser', JSON.stringify(tempUser));
            } else {
                setCurrentUser(null);
                message.error('');
            }
        } catch(err) {
            console.error(err);
        }
    }
    useEffect(() => {
        const storedUser = window.sessionStorage.getItem('currentUser');
        if (storedUser) {
            const tempUser = JSON.parse(storedUser) as User;
            setCurrentUser(tempUser);
            navigate('/');
        }
    }, [])
    const logout = () =>{
        if (currentUser) {
            logoutApi({
                accountNumber: currentUser.account
            }).then(res => {
                if (res.data.code === 300) {
                    window.sessionStorage.removeItem('currentUser');
                    setCurrentUser(null);
                    message.success('');
                    navigate('/');
                } else {
                    message.error('');
                }
            })
        } else {
            window.sessionStorage.removeItem('currentUser');
            setCurrentUser(null);
            message.success('');
            navigate('/');
        }
        
    }
    return (
        <AuthContext.Provider value={{currentUser, login, logout}} children={children}/>
    )
}