import { useEffect, useRef, useState } from "react";
import { Button, Card, Input, message } from "antd";
import PeopleList from "./Components/PeopleList/PeopleList";
import TagList from "./Components/TagList/TagList";
import PeopleModal from "./Components/PeopleModal/PeopleModal";
import { getList } from "../../Api/modules/getList";
import { getTag } from "../../Api/modules/getTag";

const { Search } = Input;

export interface PeopleItemProp {
    title: string;
    detail: string;
    tags: string[];
    id: number
}

export default function People() {
    /*
        itemitem
        
    */
    let [myList, setMyList] = useState<PeopleItemProp[]>([])
    /* 
        checkedTagtaglist
    */
    const checkedTag1 = useRef('');
    const checkedTag2 = useRef('');
    /* 
        tagliststaglist
    */
    const [tags1, setTags1] = useState<string[]>([]);
    const [tags2, setTags2] = useState<string[]>([]);
    /* 
        searchitem
    */
    let search = useRef('');
    let [currentId, setCurrentId] = useState(-1);
    let [modalVisible, setModalVisible] = useState(false);
    async function updateMyList(length: number) {
        let index;
        if (length % 3 !== 0 ){
            message.warning('');
            return;
        } else {
            index = Math.floor(length / 3);
        }
        // 
        try {
            const result = await getList({
                findValue: search.current.trim(),
                tag1: checkedTag1.current.trim(),
                tag2: checkedTag2.current.trim(),
                index,
                type: false
            });
            const code = result.data.code as number;
            if (code === 200) {
                type tmpType = {
                    title: string,
                    details: string,
                    id: number,
                    tag1: null | string,
                    tag2: null | string,
                    pictureList: null | string
                }
                const fixedRes: PeopleItemProp[] = result.data.data.map((item:tmpType)  => {
                    let ret = {
                        title: item.title,
                        detail: item.details,
                        id: item.id,
                        tags: [] as string[],
                        picture: item.pictureList
                    }
                    if (item.tag1) {
                        ret.tags.push(item.tag1);
                    }
                    if (item.tag2) {
                        ret.tags.push(item.tag2)
                    }
                    return ret;
                })
                setMyList((_myList) => _myList.slice().concat(fixedRes));
            }
        } catch (err) {
            console.log(err);
        }
    }
    async function refreshMyList() {
        setMyList([]);
        updateMyList(0);
    }
    useEffect(() => {
        getTag().then(res => {
            console.log(res)
            if (res.data.code === 200) {
                const t1 = res.data.data.Tag1 as string[];
                const t2 = res.data.data.Tag2 as string[];
                setTags1(t1);
                setTags2(t2);
            }
        })
        refreshMyList();
    }, [])
    return (
        <>
            <div className="m-4">
                <Card>
                    <div className="w-[400px] mb-4">
                        <Search
                            placeholder="input search text"
                            allowClear
                            enterButton="Search"
                            size="large"
                            onChange={(e) => {
                                search.current = e.target.value
                            }}
                            onSearch={e => {
                                if (e === '') {
                                    search.current = ''
                                }
                                refreshMyList();
                            }}
                        />
                    </div>
                    <TagList
                        tags={tags1} 
                        checkedTag={checkedTag1.current}
                        setTag={(newTag: string) => {
                            checkedTag1.current = newTag;
                        }}
                        refreshMyList={refreshMyList}
                        title='Taglist1: '
                    />
                    <TagList
                        tags={tags2} 
                        checkedTag={checkedTag2.current}
                        setTag={(newTag: string) => {
                            checkedTag2.current = newTag;
                        }}
                        refreshMyList={refreshMyList}
                        title='Taglist2: '
                    />
                </Card>
            </div>
            
            <PeopleList list={myList} modal={{setCurrentId, setModalVisible}}/>
            <Button onClick={() => {
                updateMyList(myList.length);  
            }} className="mb-4"></Button>
            <PeopleModal item={myList.find(item => item.id === currentId)} modalVisible={modalVisible} setModalVisible={setModalVisible}/>
        </>
    )
}