import { Card, Button, Tag } from 'antd'
import { MouseEventHandler } from 'react';
import { MoneyItemProp } from '../../Money'
import './MoneyItem.scss'

import { ModalObj } from './MoneyList'


export default function MoneyItem({money, modal}: {money: MoneyItemProp, modal: ModalObj}) {
    let {setCurrentId, setModalVisible} = modal;
    const handleClick:MouseEventHandler = () => {
        setCurrentId(money.id);
        setModalVisible(true);
    }
    return (
        <div className='m-4 p-[2px] text-left money-item-div'>
            <Card title={
                <>
                    <p className='mr-2'>{money.title}</p>
                    {money.tags.map((tag) => <Tag key={tag} color={'blue'}>{tag}</Tag>)}
                    <Button type='primary' className='float-right inline' onClick={handleClick}></Button>
                </>
            } className="w-full" hoverable={true}>
                <p>{money.detail}</p>
                {/* <p></p> */}
            </Card>
        </div>
    )
}