import QueryString from "qs";
import { instance } from "..";

type updateItemProp = {
    id: number,
    title: string,
    details: string,
    accountnumber: string
}

export function updateItem(prop: updateItemProp) {
    return instance.post('/idle/update', QueryString.stringify(prop));
}

export type newItemProp = {
    title: string,
    details: string,
    type: boolean,
    tag1: string,
    tag2: string,
    accountnumber: string
}

export function newItem(prop: newItemProp) {
    return instance.post('/idle/add', QueryString.stringify(prop));
}

type deleteItemProp = {
    id: number,
    accountnumber: string
}

export function deleteItem(prop: deleteItemProp) {
    return instance.post('/idle/delete', QueryString.stringify(prop));
}