import { Dispatch, MouseEventHandler, SetStateAction } from 'react';
import { Comment, Avatar, Modal, Card } from 'antd';
import { PeopleItemProp } from '../../People'

interface PeopleModalProp {
    item: PeopleItemProp | undefined;
    modalVisible: boolean;
    setModalVisible: Dispatch<SetStateAction<boolean>>
}

export default function PeopleModal({item, modalVisible, setModalVisible}: PeopleModalProp) {
    const closeModal:MouseEventHandler = () => {
        setModalVisible(false);
    }
    return (
        <>
            <Modal visible={modalVisible} onCancel={closeModal} width='60%' footer={null}>
                <div className='px-4'>
                    <Card title={item?.title}>
                        <Comment
                            author={<a>Han Solo</a>}
                            avatar={<Avatar src="https://joeschmoe.io/api/v1/random" alt="Han Solo" />}
                            content={
                                <p>
                                    {item?.detail}
                                </p>
                            }
                        />
                    </Card>
                </div>
            </Modal>
        </>
    )
}