import PublishItem from "./Components/PublishItem"
import PublishModal from "./Components/PublishModal"
import { useEffect, useState, useContext } from "react"
import { Button, message } from 'antd'
import { PlusCircleOutlined } from '@ant-design/icons'
import { getUserList } from "../../Api/modules/getList"
import { deleteItem } from "../../Api/modules/publish"
import { AuthContext } from "../../Component/AuthContext"

export interface publishItem {
    type: "money" | "people",
    usrId: number,
    id: number,
    title: string,
    detail: string,
    picture: string | null,
    publishTime: string,
    tags: string[]
}

export default function Publish() {
    let auth = useContext(AuthContext);
    //  - loadingtrue -  -  - loadingfalse
    const [publishList, setPublishList] = useState<publishItem[]>([]);
    const [modalVisible, setModalVisible] = useState(false);
    const [currentItem, setCurrentItem] = useState<publishItem | null>(null);
    const [loading, setLoading] = useState(false);
    function generateOpenModal(id:number) {
        return () => {
            setCurrentItem(null);
            const item = publishList.find(item => item.id === id);
            item && setCurrentItem(item);
            setModalVisible(true);
        }
    }
    function generateDeleteItem(id: number) {
        return () => {
            deleteItem({
                accountnumber: auth!.currentUser!.account,
                id
            }).then(res => {
                if (res.data.code === 200) {
                    message.success('');
                    initializeList();
                } else {
                    message.error('');
                }
            });
        }
    }
    function cancelModal() {
        setCurrentItem(null);
        setModalVisible(false);
    }
    async function initializeList() {
        setModalVisible(false);
        setCurrentItem(null);
        setLoading(true);
        let res;
        try {
            res = await getUserList(auth!.currentUser!.account);
        } catch (e) {
            console.log(e);
            setLoading(false);
            return;
        }
        if (res.data.code === 200) {
            type souceListType = {
                title: string,
                details: string,
                id: number,
                tag1: string,
                tag2: string,
                pictureList: null | string[],
                type: boolean,
                userid: number,
                releaseTime: string
            }
            let sourceList = res.data.data as souceListType[];
            let destList: publishItem[] = sourceList.map(item => {
                let ret: publishItem = {
                    type: item.type ? 'money' : 'people',
                    usrId: item.userid,
                    id: item.id,
                    title: item.title,
                    detail: item.details,
                    picture: item.pictureList ? item.pictureList[0] : null,
                    publishTime: item.releaseTime,
                    tags: []
                }
                if (item.tag1) {
                    ret.tags.push(item.tag1);
                }
                if (item.tag2) {
                    ret.tags.push(item.tag2);
                }
                return ret;
            })
            console.log(destList);
            setPublishList(destList);
        }
        setLoading(false);
        return;
    }
    useEffect(() => {
        initializeList();
    }, [])
    return (
        <>
            {loading ? (
                <h2>Loading</h2>
            ) : (
                <>  
                    <div className="publish-list-container flex">
                        <div className="publish-list grow">
                            {(publishList.length === 0) ? (
                                <h2 className="mt-4"></h2>
                            ) : (
                                publishList.map(item => (
                                    <PublishItem 
                                        item={item} 
                                        key={item.id} 
                                        openModal={generateOpenModal(item.id)}
                                        deleteItem={generateDeleteItem(item.id)}
                                    />
                                ))
                            )}
                        </div>
                        <div className="right-aside w-40">
                            <Button 
                                type="primary"
                                icon={<PlusCircleOutlined />} 
                                size="large" 
                                className="mt-[18px]"
                                onClick={() => {
                                    setCurrentItem(null);
                                    setModalVisible(true);
                                }}
                            >
                                
                            </Button>
                        </div>
                    </div>
                    <PublishModal
                        modalVisible={modalVisible} 
                        setModalVisible={setModalVisible}
                        currentItem={currentItem}
                        cancelModal={cancelModal}
                        refresh={initializeList}
                    />
                </>
            )}
        </>
    )
}