import { FC } from 'react';
import { BrowserRouter, Routes, Route } from 'react-router-dom';
import MySider from './Layout/Layout';
import AuthContextProvider from './Component/AuthContext';
import AccessControl from './Component/AccessControl';
import LoginPage from './View/LoginPage';
import { Navigate } from 'react-router-dom';
import { routes } from './Route';
import NotFound from './View/NotFound';
import './App.css';



const App: FC = () => (
  <div className="App">
    <BrowserRouter>
      <AuthContextProvider>
          <Routes>
              <Route 
                path='/' 
                element={
                  <AccessControl fallback={<Navigate to='/enter'/>}>
                    <MySider></MySider>
                  </AccessControl>
                }
              >
                {routes.map(route => (
                  <Route key={route.path} path={route.path} element={<route.component />}/>
                ))}
                <Route index element={<Navigate to={'/people'}/>}/>
              </Route>
            <Route path='/enter' element={<LoginPage/>}></Route>
            {/* <Route index element={<NotFound />} /> */}
          </Routes>
      </AuthContextProvider>
    </BrowserRouter>
  </div>
);

export default App;