import { Form , Input, Button, Select, Radio, Upload, message } from 'antd'
import { publishItem } from '../Publish'
import { updateItem, newItem, newItemProp } from '../../../Api/modules/publish';
import { useContext, useEffect } from 'react';
import { AuthContext } from '../../../Component/AuthContext';

interface createChangeFormProp {
    currentItem: publishItem | null,
    refresh: () => Promise<void>
}

export default function CreateChangeForm({currentItem, refresh}: createChangeFormProp) {
    const auth = useContext(AuthContext);
    let [form] = Form.useForm();
    const isCreate = !currentItem;
    const handleFinish = (e: any) => {
        if (!isCreate) {
            // 
            updateItem({
                accountnumber: auth!.currentUser!.account,
                title: e.title,
                details: e.detail,
                id: currentItem.id
            }).then(res => {
                if (res.data.code === 200) {
                    message.success('');
                    refresh();
                } else {
                    message.error('');
                }
            })
        } else {
            // 
            let tmpProp: newItemProp = {
                title: e.title.trim(),
                details: e.detail.trim(),
                tag1: e.tag1 ? e.tag1.trim() : '',
                tag2: e.tag2 ? e.tag2.trim() : '',
                type: e.type === 'money' ? true : false,
                accountnumber: auth!.currentUser!.account
            }
            newItem(tmpProp).then(res => {
                if (res.data.code === 200) {
                    message.success('');
                    refresh();
                } else {
                    message.error('');
                }
            })
        }
    }
    useEffect(() => {
        form.resetFields();
        return () => {
            form.resetFields();
        }
    })
    return (
        <>
            <Form
                labelCol={{ span: 4, offset: 2 }}
                wrapperCol={{ span: 14, offset: 0 }}
                onFinish={handleFinish}
                form={form}
            >
                <Form.Item 
                    name='title'
                    label="Title"
                    rules={[{ required: true, message: 'Please enter your title!' }]}
                >
                    <Input/>
                </Form.Item>
                <Form.Item 
                    name='detail'
                    label="Detail"
                    rules={[{ required: true, message: 'Please enter your detail!' }]}
                >
                    <Input/>
                </Form.Item>
                
                {isCreate && (
                    <>
                        <Form.Item 
                            name='tag1'
                            label="Tag1"
                        >
                            <Input/>
                        </Form.Item>
                        <Form.Item 
                            name='tag2'
                            label="Tag2"
                        >
                            <Input/>
                        </Form.Item>
                        <Form.Item
                            name="type"
                            label=""
                            rules={[{ required: true, message: 'Please pick an item!' }]}
                        >
                            <Radio.Group>
                                <Radio.Button value="money"></Radio.Button>
                                <Radio.Button value="people"></Radio.Button>
                            </Radio.Group>
                        </Form.Item>
                    </>
                )}
                <Form.Item wrapperCol={{ offset: 6, span: 16 }}>
                    <Button type="primary" htmlType="submit">
                        Submit
                    </Button>
                </Form.Item>
            </Form>
        </>
    )
}