import { Dispatch, SetStateAction, useEffect, useState } from 'react'
import { MouseEventHandler } from 'react';
import CreateChangeForm from './CreateChangeForm';
import { Comment, Avatar, Modal, Card, Image, Button, Form, Spin } from 'antd';
import { publishItem } from '../Publish'

interface PublishModalProp {
    modalVisible: boolean, 
    setModalVisible: Dispatch<SetStateAction<boolean>>,
    currentItem: publishItem | null,
    cancelModal: VoidFunction,
    refresh: () => Promise<void>
}

export default function PublishModal({modalVisible, setModalVisible, currentItem, cancelModal, refresh}: PublishModalProp) {
    return (
        <>
            <Modal 
                visible={modalVisible} 
                width='50%' 
                onCancel={cancelModal}
                footer={null}
            >
                <div className='px-4'>
                    <Card>
                        <CreateChangeForm currentItem={currentItem} refresh={refresh}/>
                    </Card>
                </div>
            </Modal>
        </>
    )
}