import { Row, Col, Form, Input, Button, Typography, message, Layout } from "antd";
import  { MouseEventHandler, useReducer, useContext } from 'react'
import { useNavigate } from "react-router-dom";
import { AuthContext } from "../Component/AuthContext";
import { register } from "../Api/modules/user";
import '../style/LoginPage.scss'
const { Header, Content } = Layout;
const { Title } = Typography;

export default function LoginPage() {
    const [form] = Form.useForm();
    let auth = useContext(AuthContext);
    let navigate = useNavigate();
    const [isLoginPage, setIsLoginPage] = useReducer((isLoginPage) => !isLoginPage, true);
    const toggleLogin: MouseEventHandler<HTMLElement> = () => {
        form.resetFields();
        setIsLoginPage();
    }
    const onFinish = async (e: any) => {
        form.resetFields();
        if (isLoginPage) {
            await auth?.login({
                accountNumber: e.account,
                pwd: e.password
            });
            navigate('/');
        } else {
            register({
                username: e.username,
                accountNumber: e.account,
                pwd: e.password,
            }).then(res => {
                if (res.data.code === 200) {
                    message.success('');
                    form.resetFields();
                    setIsLoginPage();
                }
            })
        }
    };
    return (
        <>
            <div className="login-container">
                <Row>
                    <Col span={8}>
                    </Col>
                    <Col span={8} className="bg-blue-100 h-[66vh] rounded-b-lg shadow-2xl block form-container">
                        <Button type="primary" className="float-left" size="small" onClick={toggleLogin}>
                            {isLoginPage ? "Register" : "Login"}
                        </Button>
                        <div className="my-20">
                            <Title className="mb-28">
                                
                            </Title>
                        </div>
                        <Form
                            labelCol={{ span: 4, offset: 2 }}
                            wrapperCol={{ span: 14, offset: 0 }}
                            autoComplete="off"
                            onFinish={onFinish}
                            form={form}
                        >
                            { isLoginPage ? null :
                                <Form.Item
                                    label="Username"
                                    name="username"
                                    rules={[{ required: true, message: 'Please input your username!' }]}
                                >
                                    <Input />
                                </Form.Item>
                            }
                            <Form.Item
                                name="account"
                                label="Account"
                                rules={[
                                    { required: true, message: 'Please input your acccount!' }
                                ]}
                            >
                                <Input />
                            </Form.Item>
                            <Form.Item
                                label="Password"
                                name="password"
                                rules={[{ required: true, message: 'Please input your password!' }]}
                            >
                                <Input.Password />
                            </Form.Item>
                            { isLoginPage ? null : 
                                <Form.Item
                                    name="confirm"
                                    label="Confirm Password"
                                    dependencies={['password']}
                                    rules={[
                                        { required: true, message: 'Please input your password!' },
                                        ({ getFieldValue }) => ({
                                            validator(_, value) {
                                            if (!value || getFieldValue('password') === value) {
                                                return Promise.resolve();
                                            }
                                            return Promise.reject(new Error('The two passwords that you entered do not match!'));
                                            },
                                        })
                                    ]}
                                >
                                    <Input.Password />
                                </Form.Item>
                            }
                            <Form.Item wrapperCol={{ offset: 4, span: 16 }}>
                                <Button type="primary" htmlType="submit">
                                    {isLoginPage ? 'Login' : 'Register'}
                                </Button>
                            </Form.Item>
                        </Form>
                    </Col>
                    <Col span={8}>
                    </Col>
                </Row>
            </div>
        </>
    )
}