import { Dispatch, SetStateAction } from 'react';
import { MoneyItemProp } from '../../Money'
import MoneyItem from './MoneyItem';

import FilterRender from '../../../../Component/FilterRender';

export interface ModalObj {
    setModalVisible: Dispatch<SetStateAction<boolean>>
    setCurrentId: Dispatch<SetStateAction<number>>
}

interface MoneyListProp {
    list: MoneyItemProp[];
    modal: ModalObj
}

export default function MoneyList({list, modal}: MoneyListProp) {
    return FilterRender(list,
        (item => (<MoneyItem money={item} key={item.id} modal={modal}/>)));
}
