import { Card, Tag } from 'antd'

const { CheckableTag } = Tag;

interface TagListProp {
    tags?: string[];
    checkedTag: string;
    setTag: (newTag: string) => void;
    title: string;
    refreshMyList: VoidFunction;
}



export default function TagList({tags = [], checkedTag, setTag, title, refreshMyList}: TagListProp) {
    function setTagByName(tagName: string) {
        if (tagName === checkedTag) {
            return () => setTag('');
        } else {
            return () => setTag(tagName);
        }
    }
    return (
        <div className="text-left">
            <Card type='inner'>
                <p className='inline-block'>{title}</p>
                {/* {tags.map(tag => <TagItem tag={tag} key={tag} setTagByName={setTagByName(tag)}></TagItem>)} */}
                {tags.map(tag => (
                    <CheckableTag 
                        checked={tag === checkedTag} 
                        onClick={() => {
                            setTagByName(tag)();
                            setTimeout(() => {
                                refreshMyList();
                            }, 0);
                        }}
                        key={tag}
                    >{tag}</CheckableTag>
                ))}
            </Card>
        </div>
    )
}