import People from "../View/People/People"
import Money from "../View/Money/Money"
import Publish from "../View/Publish/Publish"

type route = {
    path: string,
    name:string,
    component: () => JSX.Element
}

export const routes: route[] = [
    {
        name: 'People',
        path: '/people',
        component: People
    },
    {
        name: 'Money',
        path: '/money',
        component: Money
    },
    {
        name: 'Publish',
        path: '/publish',
        component: Publish
    }
]