import { Card, Button, Tag } from 'antd'
import { MouseEventHandler } from 'react';
import { PeopleItemProp } from '../../People'
import './PeopleItem.scss'

import { ModalObj } from './PeopleList'


export default function PeopleItem({people, modal}: {people: PeopleItemProp, modal: ModalObj}) {
    let {setCurrentId, setModalVisible} = modal;
    const handleClick:MouseEventHandler = () => {
        setCurrentId(people.id);
        setModalVisible(true);
    }
    return (
        <div className='m-4 p-[2px] text-left people-item-div'>
            <Card title={
                <>
                    <p className='mr-2'>{people.title}</p>
                    {people.tags.map((tag) => <Tag key={tag} color={'blue'}>{tag}</Tag>)}
                    <Button type='primary' className='float-right inline' onClick={handleClick}></Button>
                </>
            } className="w-full" hoverable={true}>
                <p>{people.detail}</p>
            </Card>
        </div>
    )
}