import './Layout.scss'
import { Component } from 'react';
import { Layout, Menu, Breadcrumb, Avatar, Dropdown, Button } from 'antd';
import {
  DesktopOutlined,
  PieChartOutlined,
  FileOutlined,
  TeamOutlined,
  UserOutlined,
  ContactsTwoTone
} from '@ant-design/icons';
import { Link, Outlet } from 'react-router-dom';
import { useContext } from 'react';
import { AuthContext } from '../Component/AuthContext';
import { routes } from '../Route';

const { Header, Content, Footer, Sider } = Layout;
const { SubMenu } = Menu;

export default function MySider() {
    const auth = useContext(AuthContext);

    return (
      <div className="layout">
        <Layout style={{ minHeight: '100vh' }}>
            <Header>
              <div className="header-left float-left">
                <div className='clear-both inline-block'>
                  <ContactsTwoTone 
                      style={{fontSize: '1.9rem'}}
                      className="text-black"
                  />
                </div>
                <p className='text-black inline-block ml-3'>
                    
                </p>
              </div>
              <div className="header-right float-right">
                <Avatar src="https://joeschmoe.io/api/v1/random"/>
                <Dropdown 
                  overlay={<Menu>
                    <Menu.Item key='log-out-drop-down'>
                      <a onClick={() => {
                        auth?.logout();
                        return false;
                      }}>
                        Log out
                      </a>
                    </Menu.Item>
                  </Menu>}
                  placement='bottomRight'
                  arrow={{pointAtCenter: true}}
                >
                  <p className='inline-block mb-0 ml-4 underline cursor-pointer'>
                    {auth?.currentUser?.username}
                  </p>
                </Dropdown>
              </div>
            </Header>
            <Layout>
                <Sider theme="light">
                  <Menu 
                    theme="light" 
                    defaultSelectedKeys={[routes[0].path]} 
                    mode="inline"
                  >
                      {routes.map(route => (
                        <Menu.Item key={route.path} icon={<TeamOutlined />}>
                          <Link to={route.path}>{route.name}</Link>
                        </Menu.Item>
                      ))}
                  </Menu>
                </Sider>
                <Content>
                  <Outlet/>
                </Content>
            </Layout>
        </Layout>
      </div>
    );
  }